.app{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 40px 24px;
}

.app__searchForm{
  margin-top: 18px;
}
.app__input{
  padding: 6px 16px;
  min-width: 200px;
  max-width: 100vw;
  font-size: 15px;
}

.app__submit{
  padding: 6px 16px;
  font-size: 15px;
  margin-left: 6px;
  background-color: blue;
  border: 1px solid blue;
  border-radius: 4px;
  color: white;
}
.app__cuisineSelect {
  padding: 8px 16px;
  border: 2px solid #ccc;
  border-radius: 4px;
  background-color: white;
  font-size: 16px;
  margin: 10px 5px;
  cursor: pointer;
  transition: border-color 0.3s ease-in-out;
}

.app__cuisineSelect:focus {
  border-color: #007BFF; 
  outline: none; 
}
